import { ipcRenderer } from "electron";

class Logger {
  constructor() {
    this.logQueue = [];
    this.isProcessing = false;
  }

  formatMessage(level, message, details = null, source = null) {
    const timestamp = new Date().toISOString();
    const sourceInfo = source ? ` [${source}]` : '';
    let formattedMessage = `[${timestamp}] ${level.toUpperCase()}${sourceInfo}: ${message}`;
    
    if (details) {
      // If details is an Error object, extract useful information
      if (details instanceof Error) {
        details = {
          message: details.message,
          stack: details.stack,
          ...details
        };
      }
      
      // Format details as pretty JSON, but handle circular references
      try {
        const detailsStr = JSON.stringify(details, (key, value) => {
          if (key === 'stack' && typeof value === 'string') {
            return value.split('\n');
          }
          return value;
        }, 2);
        formattedMessage += '\n' + detailsStr;
      } catch (error) {
        formattedMessage += '\n[Error formatting details: ' + error.message + ']';
      }
    }
    
    return formattedMessage;
  }

  async processQueue() {
    if (this.isProcessing || this.logQueue.length === 0) return;
    
    this.isProcessing = true;
    
    while (this.logQueue.length > 0) {
      const logEntry = this.logQueue.shift();
      await ipcRenderer.invoke("write-log", logEntry);
    }
    
    this.isProcessing = false;
  }

  async log(level, message, details = null, source = null) {
    const formattedMessage = this.formatMessage(level, message, details, source);
    
    // Log to console with appropriate styling
    const consoleStyles = {
      error: 'color: #ff0000',
      warn: 'color: #ffa500',
      info: 'color: #0000ff'
    };
    
    // Log to console
    switch (level.toLowerCase()) {
      case "error":
        console.error(`%c${message}`, consoleStyles.error, details || '');
        break;
      case "warn":
        console.warn(`%c${message}`, consoleStyles.warn, details || '');
        break;
      default:
        console.log(`%c${message}`, consoleStyles.info, details || '');
    }
    
    // Queue log for file writing
    this.logQueue.push(formattedMessage);
    this.processQueue();
  }

  info(message, details = null, source = null) {
    return this.log("info", message, details, source);
  }

  error(message, details = null, source = null) {
    return this.log("error", message, details, source);
  }

  warn(message, details = null, source = null) {
    return this.log("warn", message, details, source);
  }
}

export const logger = new Logger();
